@import '../mixins.scss';

.about-info-container {
  display: flex;
  align-items: center;
  margin: 12px 0;
  

  .sm-logo {
    width: 36px;
    height: auto;
    margin-right: 10px;
  }

  p {
    margin: 0;
  }

  @include responsive('tablet') {
    .sm-logo {
      width: 48px;
      height: auto;
      margin-right: 20px;
    }
  }
}

.bottom-line {
  display: block;
  width: 100%; 
  height: 1px; 
  background-color: white; 
  margin-top: 5px;
}