.card-button {
  --color: #560bad;
  font-family: inherit;
  display: inline-block;
  width: auto;
  height: 2.6em;
  padding: 0 10px;
  line-height: 2.5em;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  border: 2px solid #fff; /* Borda branca */
  transition: color 0.5s, background-color 0.5s, border-color 0.5s;
  z-index: 1;
  font-size: 17px;
  border-radius: 6px;
  font-weight: 500;
  color: #fff;
  background: transparent;

  a:hover {
    text-decoration: none;
    color: #672471;
  }
}

.card-button:before {
  content: "";
  position: absolute;
  z-index: -1;
  background: transparent;
  height: 150px;
  width: 200px;
  border-radius: 50%;
  transition: all 0.7s;
}

.card-button:hover {
  color: var(--color); 
  background-color: #fff; 
  border-color: var(--color);
}

.card-button:hover:before {
  top: -30px;
  left: -30px;
  background: #fff;
}

.card-button:active:before {
  background: #3a0ca3;
  transition: background 0s;
}

.card-button:before {
  top: 100%;
  left: 100%;
  transition: all 0.7s;
}
